<template>
  <div id="main">
    <el-card>
    <mavon-editor ref="md" v-model="value" :ishljs="true" @imgAdd="$imgAdd" @imgDel="$imgDel" />
    </el-card>
  </div>
</template>
<script>
import axios from 'axios'
// markdown
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
// use
// Vue.use(mavonEditor)
export default {
  name: 'amrkdown',
  components: {
    mavonEditor
  },
  methods: {
    // @imgAdd event
    $imgAdd(pos, $file) {
      // ..
      var formdata = new FormData()
      formdata.append('image', $file)
      axios({
        url: 'server url',
        method: 'post',
        data: formdata,
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then((url) => {
        // .url![...](./0) -> ![...](url)
        /**
         * $vm mavonEditor
         * 1. : `import {mavonEditor} from ...` `$vm``mavonEditor`
         * 2. $refs: htmlref : `<mavon-editor ref=md ></mavon-editor>`$vm` `this.$refs.md`
         */
        this.$refs.md.$img2Url(pos, url)
      })
    }
  }
}
</script>



